// src/components/ProductCard.jsx

import React, { useCallback, useMemo } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { addToCart } from '../redux/cartSlice';
import { toggleWishlistItem } from '../redux/wishlistSlice';
import { selectWishlistItems } from '../redux/wishlistSlice';
import { selectCartItems } from '../redux/cartSlice';
import formatPrice from '../utils/formatPrice';
import { motion, AnimatePresence } from 'framer-motion';

const ProductCard = ({ product }) => {
  const dispatch = useDispatch();
  const wishlistItems = useSelector(selectWishlistItems);
  const cartItems = useSelector(selectCartItems);

  //      
  const isInWishlist = useMemo(() => wishlistItems.some(item => item.id === product._id), [wishlistItems, product._id]);
  const isInCart = useMemo(() => cartItems.some(item => item.id === product._id), [cartItems, product._id]);
  const hasStock = useMemo(() => product.stock === undefined || product.stock > 0, [product.stock]);

  const addToCartHandler = useCallback((e) => {
    e.preventDefault();
    e.stopPropagation();
    if (!isInCart && hasStock) {
      console.log('Adding to cart:', product._id); //     
      dispatch(addToCart({
        id: product._id,
        name: product.name,
        price: Number(product.price) || 0, //    number
        image: product.images ? product.images[0] : product.image,
        qty: 1 //   qty    Redux
      }));
    }
  }, [dispatch, isInCart, hasStock, product]);

  const toggleWishlistHandler = useCallback((e) => {
    e.preventDefault();
    e.stopPropagation();
    console.log('Toggling wishlist:', product._id); // 
    dispatch(toggleWishlistItem({
      id: product._id,
      name: product.name,
      price: Number(product.price) || 0,
      image: product.images ? product.images[0] : product.image
    }));
  }, [dispatch, product]);

  //   (   hover,  )
  const cardVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { 
      opacity: 1, 
      y: 0, 
      transition: { duration: 0.4, ease: 'easeOut' } 
    },
    hover: { 
      scale: 1.02, 
      boxShadow: '0 20px 40px rgba(0,0,0,0.3)',
      transition: { duration: 0.2, ease: 'easeInOut' }
    }
  };

  //     (   glow)
  const successPulse = {
    scale: [1, 1.1, 1],
    boxShadow: [
      '0 4px 8px rgba(0,0,0,0.2)',
      '0 0 20px var(--green), 0 0 30px var(--green)',
      '0 4px 8px rgba(0,0,0,0.2)'
    ],
    transition: { duration: 0.4, ease: 'easeInOut' }
  };

  //    ( + glow  pixelated )
  const wishlistPulse = {
    scale: [1, 1.2, 1],
    rotate: [0, 180, 360],
    boxShadow: [
      '0 2px 8px rgba(255,0,127,0.3)',
      '0 0 25px var(--pink), 0 0 35px var(--pink)',
      '0 2px 8px rgba(255,0,127,0.3)'
    ],
    transition: { duration: 0.6, ease: 'easeInOut' }
  };

  //  :     ( ,    UI)
  const CartIcon = isInCart ? '' : (hasStock ? '+' : '');
  const HeartIcon = isInWishlist ? '' : ''; //  filled (,    glow),  outline ()

  // Badge  condition: ,  , top-right,  
  const ConditionBadge = () => (
    <motion.span
      initial={{ opacity: 0, scale: 0.8 }}
      animate={{ opacity: 1, scale: 1 }}
      style={{
        position: 'absolute',
        top: 8,
        right: 8,
        background: 'transparent',
        color: product.condition === 'new' ? 'var(--green)' : '#ff9800', //    
        fontSize: 8, //  
        fontWeight: 'bold',
        textShadow: '0 0 3px currentColor', //  glow  
        padding: 0,
        zIndex: 5
      }}
    >
      {product.condition === 'new' ? '' : '/'}
    </motion.span>
  );

  return (
    <motion.div 
      className="card surface shimmer"
      variants={cardVariants}
      initial="hidden"
      animate="visible"
      whileHover="hover"
      style={{ 
        height: 'auto', 
        display: 'flex', 
        flexDirection: 'column',
        overflow: 'hidden',
        borderRadius: 'var(--radius)',
        position: 'relative',
        zIndex: 1
      }}
    >
      {/* Badge   /   RETRO */}
      <AnimatePresence>
        {!hasStock && (
          <motion.span 
            className="badge" 
            initial={{ scale: 0, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            exit={{ scale: 0, opacity: 0 }}
            style={{ 
              background: 'linear-gradient(180deg, #666, #444)',
              color: 'var(--white)',
              fontSize: 10,
              padding: '6px 10px',
              top: 12,
              left: 12,
              zIndex: 5
            }}
          >
            Out of Stock
          </motion.span>
        )}
        {product.condition && <ConditionBadge />}
      </AnimatePresence>

      {/* Image   hover   img */}
      <div 
        className="card-img" 
        style={{ 
          height: 200, 
          position: 'relative',
          background: 'linear-gradient(180deg, #211733, #0B0B0B)',
          borderBottom: '1px dashed rgba(255,255,255,.03)'
        }}
      >
        <Link 
          to={`/product/${product._id}`}
          style={{ display: 'block', height: '100%', pointerEvents: 'auto' }}
        >
          <motion.img 
            src={product.images ? product.images[0] : product.image} 
            alt={product.name}
            initial={{ scale: 0.95, opacity: 0.8 }}
            whileHover={{ scale: 1.05, opacity: 1 }}
            transition={{ duration: 0.3, ease: 'easeOut' }}
            style={{ 
              maxWidth: '100%', 
              maxHeight: '100%', 
              objectFit: 'contain',
              imageRendering: 'pixelated',
              filter: 'drop-shadow(0 6px 16px rgba(0,0,0,.3))',
              display: 'block',
              pointerEvents: 'none' //  events  img,     
            }}
          />
        </Link>
      </div>

      {/* Body */}
      <div 
        className="card-body" 
        style={{ 
          padding: '16px 16px 20px', 
          flexGrow: 1, 
          display: 'flex', 
          flexDirection: 'column',
          justifyContent: 'space-between',
          gap: 8,
          position: 'relative',
          zIndex: 10
        }}
      >
        {/* Name  hover   h3 */}
        <Link 
          to={`/product/${product._id}`}
          style={{ textDecoration: 'none' }}
        >
          <motion.h3 
            className="h2 mono" 
            whileHover={{ color: 'var(--turquoise)' }}
            style={{ 
              fontSize: '14px', 
              margin: 0, 
              color: 'var(--yellow)', 
              lineHeight: 1.3,
              transition: 'color 0.2s ease',
              textShadow: 'none',
              pointerEvents: 'auto'
            }}
          >
            {product.name}
          </motion.h3>
        </Link>

        {/* Additional Info */}
        <div style={{ display: 'grid', gap: 4, fontSize: 10, opacity: 0.8 }}>
          {product.category && (
            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
              <span style={{ color: 'var(--pink)' }}>:</span>
              <span>{product.category.toUpperCase()}</span>
            </div>
          )}
          {product.genre && (
            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
              <span style={{ color: 'var(--turquoise)' }}>:</span>
              <span>{product.genre.charAt(0).toUpperCase() + product.genre.slice(1)}</span>
            </div>
          )}
          {product.stock !== undefined && (
            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
              <span style={{ color: 'var(--green)' }}>:</span>
              <span>{product.stock > 0 ? `${product.stock} .` : ''}</span>
            </div>
          )}
        </div>

        {/* Price   hover */}
        <motion.div 
          className="p" 
          whileHover={{ scale: 1.02 }}
          style={{ 
            fontSize: 14, 
            fontWeight: 'bold', 
            color: 'var(--yellow)', 
            margin: '8px 0 0 0',
            textAlign: 'center',
            textShadow: 'none'
          }}
        >
          {formatPrice(Number(product.price) || 0)} {/*   */}
        </motion.div>

        {/* Buttons    hover ,   gap */}
        <div style={{ 
          display: 'flex', 
          gap: 12, //  gap   ( 8,  12     )
          marginTop: 'auto',
          justifyContent: 'center',
          zIndex: 20, //    
          pointerEvents: 'auto'
        }}>
          {/*     3D inset   pixelated glow */}
          <motion.button
            className={`btn btn-green ${isInCart ? 'in-cart' : ''}`}
            onClick={addToCartHandler}
            variants={hasStock && !isInCart ? { 
              hover: { 
                scale: 1.05, 
                boxShadow: '0 6px 12px rgba(76,175,80,0.4), inset 0 1px 0 rgba(255,255,255,0.2)' //  inset  3D
              }, 
              tap: { scale: 0.95 } 
            } : {}}
            animate={isInCart ? {} : successPulse}
            disabled={!hasStock || isInCart}
            style={{ 
              flex: 1, 
              padding: '8px 12px', 
              fontSize: 10,
              minHeight: 32,
              opacity: (!hasStock || isInCart) ? 0.6 : 1,
              background: (!hasStock || isInCart) ? 'linear-gradient(180deg, #666, #444)' : 'linear-gradient(180deg, var(--green), var(--green-2))',
              borderColor: (!hasStock || isInCart) ? '#666' : 'var(--green)',
              cursor: (!hasStock || isInCart) ? 'not-allowed' : 'pointer',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              gap: 4,
              position: 'relative',
              overflow: 'hidden',
              boxShadow: (!hasStock || isInCart) ? '0 2px 4px rgba(0,0,0,0.2)' : '0 4px 8px rgba(76,175,80,0.3), inset 0 1px 0 rgba(255,255,255,0.1)',
              pointerEvents: 'auto',
              zIndex: 20,
              fontWeight: 'bold',
              borderRadius: '8px', //    
              imageRendering: 'pixelated', //      
              textShadow: (!hasStock || isInCart) ? 'none' : '0 0 3px rgba(255,255,255,0.8)' // Glow  
            }}
          >
            {CartIcon}
            <span style={{ marginLeft: 2 }}>{isInCart ? ' ' : (!hasStock ? '' : ' ')}</span>
          </motion.button>

          {/*       hover   glow */}
          <motion.button
            className={`btn btn-wish ${isInWishlist ? 'active' : ''}`}
            onClick={toggleWishlistHandler}
            variants={{ 
              hover: { 
                scale: 1.05, 
                rotate: 360, 
                boxShadow: '0 0 20px var(--pink), 0 4px 12px rgba(255,0,127,0.4), inset 0 1px 0 rgba(255,255,255,0.2)' //  glow + inset
              }, 
              tap: { scale: 0.95 } 
            }}
            animate={isInWishlist ? wishlistPulse : {}}
            style={{ 
              width: 32,
              height: 32,
              padding: 0,
              minHeight: 32,
              background: isInWishlist ? 'linear-gradient(180deg, var(--pink), var(--pink-2))' : 'linear-gradient(180deg, #666, #888)',
              borderColor: isInWishlist ? 'var(--pink)' : '#666',
              borderRadius: '50%',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              boxShadow: isInWishlist ? '0 0 15px rgba(255,0,127,0.6), 0 2px 8px rgba(255,0,127,0.4), inset 0 1px 0 rgba(255,255,255,0.1)' : '0 2px 4px rgba(0,0,0,0.2)',
              color: isInWishlist ? 'var(--white)' : '#ccc', //   filled (  ),   unfilled
              textShadow: isInWishlist ? '0 0 3px rgba(255,255,255,0.8)' : 'none', //  glow   
              pointerEvents: 'auto',
              zIndex: 20,
              fontSize: 16, //    
              fontWeight: 'bold',
              imageRendering: 'pixelated', //    
              transition: 'all 0.3s ease' //    
            }}
          >
            {HeartIcon}
          </motion.button>
        </div>
      </div>
    </motion.div>
  );
};

export default ProductCard;